.file-folder {
	width: 125px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
}

.file-folder img {
	align-self: auto;
}

.file-folder div {
	text-align: center;
}

.file-folder:hover {
	background-color: lightgray;
}

.new-file {
	align-self: center;
	height: min-content;
}

.new-file:hover {
	filter: invert(0.1);
}

.menu {
	background-color: white;
	border: 1px solid lightgray;
	border-radius: 5px;
	padding: 0;
}

.danger {
	color: red;
}

.menu-item {
	text-align: center;
	margin: 0;
	padding: 15px 20px;
	cursor: pointer;
	user-select: none;
}

.menu-item:hover {
	background-color: lightgray;
}

.menu-divider {
	background-color: lightgray;
	height: 1px;
	margin: 0;
}
